.swiper {
  width: 100%;
  height: 100%;
}

.swiper-btn-next {
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 2;
  width: 50px;
  height: 50px;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  border: 3px solid #000;
}
.swiper-btn-next::before {
  content: ">";
  position: absolute;
  font-size: 36px; /* Размер символа */
  display: inline-block; /* Отображаем символ как блок */
  top: 45%;
  right: 45%;
  transform: translate(50%, -50%);
}

.swiper-button-next::after {
  display: none;
}

